@import "../../styles/variables.scss";

.contact-us-page {
  background: $black;
  color: $white;
  padding-top: 200px;

  @media screen and (max-width: $breakpoint-md) {
    padding-top: 130px;
  }

  section {
    margin: 0 auto;
    max-width: 1240px;
    padding: 0 300px 150px 80px;

    .text-h1 {
      margin-bottom: 40px;
    }

    .content {
      padding-left: 120px;
    }

    @media screen and (max-width: $breakpoint-xl) {
      padding: 0 80px 150px 80px;   
    }

    @media screen and (max-width: $breakpoint-md) {
      padding: 0 40px 110px 40px;

      .content {
        padding-left: 80px;
      }
    }
    
    @media screen and (max-width: $breakpoint-sm) {
      padding: 0 20px 70px 20px;

      .content {
        padding-left: 50px;
      }
    }

    &.want-to-talk {
      .content {
        display: flex;

        & > div {
          width: 50%;
          flex-basis: 50%;
          padding-right: 20px;

          .text-subheading {
            margin-bottom: 20px;
          }

          .link {
            color: $green;
          }
        }

        @media screen and (max-width: $breakpoint-xs) {
          flex-direction: column;

          & > div {
            width: 100%;
            flex-basis: 100%;
            padding: 0 0 50px 0;
          }
        }
      }
    }

    &.get-in-touch {
      form {
        .inline-fields {
          display: flex;

          & > div {
            width: calc(50% - 15px);

            &:nth-child(odd) {
              margin-right: 15px;
            }

            &:nth-child(even) {
              margin-left: 15px;
            }
          }
        }

        .text-area {
          margin-bottom: 40px;
        }
      }

      @media screen and (max-width: $breakpoint-md) {
        form {
          .text-area {
            margin-bottom: 30px;
          }
        }
      }
    }
  }
}