@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.services-page {
  background: $black;
  color: $white;
  padding-top: 200px;

  @media screen and (max-width: $breakpoint-md) {
    padding-top: 160px;
  }
  
  @media screen and (max-width: $breakpoint-sm) {
    padding-top: 120px;
  }

  section {
    margin: 0 auto;
    max-width: 1240px;
    padding: 0 160px 150px 80px;

    .text-h1 {
      margin-bottom: 70px;
    }

    .content {
      padding-left: 120px;
    }

    @media screen and (max-width: $breakpoint-xl) {
      padding: 0 80px 150px 80px;   
    }

    @media screen and (max-width: $breakpoint-md) {
      padding: 0 40px 110px 40px;

      .content {
        padding-left: 80px;
      }

      .text-h1 {
        margin-bottom: 30px;
      }
    }
    
    @media screen and (max-width: $breakpoint-sm) {
      padding: 0 20px 80px 20px;

      .content {
        padding-left: 50px;
      }
    }

    &.expertise, &.how-we-work {
      .content {
        display: flex;
        flex-wrap: wrap;

        .service-wrap {
          width: 33.33%;
          padding-right: 20px;

          .service {
            max-width: 200px;

            .image {
              margin-bottom: 50px;

              img {
                height: 60px;
              }
            }

            .separator {
              margin: 15px 0 50px 0;
              width: 80px;
              height: 5px;
              background: $green;
            }
          }

          &:nth-child(-n+3) {
            padding-bottom: 100px;
          }
        }
        
        @media screen and (max-width: $breakpoint-md) {
          .service-wrap {
            .service {
              max-width: 160px;

              .image {
                margin-bottom: 20px;

                img {
                  height: 40px;
                }
              }

              .separator {
                margin: 15px 0 20px 0;
              }
            }
          }
        }
        
        @media screen and (max-width: $breakpoint-xs) {
          .service-wrap {
            width: 50%;
          }
        }
      }
    }

    &.partners {
      .content {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          height: 40px;
          max-width: 160px;

          &:not(:last-child) {
            margin-right: 100px;
          }
        }
      }
      @media screen and (max-width: $breakpoint-md) {
        .text-h1 {
          margin-bottom: 50px;
        }

        .content {
          padding-left: 0;

          img {
            height: 30px;
            max-width: 120px;
            
            &:not(:last-child) {
              margin-right: 50px;
            }
          }
        }
      }

      @media screen and (max-width: $breakpoint-xs) {
        .content {
          flex-direction: column;

          img {
            &:not(:last-child) {
              margin-right: 0;
              margin-bottom: 50px;
            }
          }
        }
      }
    }

    &.contact-us {
      .content {
        .text {
          max-width: 480px;
          padding-bottom: 30px;
        }

        .link {
          color: $green;
          display: flex;
          height: 28px;

          svg {
            margin: 5px 0 0 8px;

            & * {
              fill: $green;
            }
          }

          span {
            border-bottom: 2px solid transparent;
            @include transition;
          }
          
          &:hover {
            span {
              border-color: $green;
            }
          }
        }

        @media screen and (max-width: $breakpoint-sm) {
          .text {
            padding-bottom: 20px;
          }

          .link {
            svg {
              margin-top: 2px;
            }
          }
        }
      }
    }
  }
}