@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.about-us-page {
  background: $black;
  color: $white;

  .page-cover {
    display: flex;
    flex-direction: column;

    .image {
      width: 100%;
      height: 75vh;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .motto {
      padding: 70px 100px 150px 100px;
      align-self: flex-end;

      .text-xl {
        text-align: right;
        max-width: 1024px;
      }
    }

    @media screen and (max-width: $breakpoint-xl) {
      .motto {
        padding: 70px 80px 150px 80px;
      }
    }

    @media screen and (max-width: $breakpoint-md) {
      .motto {
        padding: 70px 40px 110px 40px;

        .text-xl {
          max-width: 480px;
        }
      }
    }

    @media screen and (max-width: $breakpoint-sm) {
      padding-top: 85px;

      .image {
        height: auto;
      }

      .motto {
        padding: 20px 20px 0 20px;
      }
    }
  }

  section {
    margin: 0 auto;
    max-width: 1240px;
    padding: 0 300px 150px 80px;

    .text-h1 {
      margin-bottom: 30px;
    }

    .content {
      padding-left: 120px;
    }
    
    @media screen and (max-width: $breakpoint-xl) {
      padding: 0 80px 150px 80px;   
    }

    @media screen and (max-width: $breakpoint-md) {
      padding: 0 40px 110px 40px;

      .content {
        padding-left: 80px;
      }
    }
    
    @media screen and (max-width: $breakpoint-sm) {
      padding: 70px 20px 0 20px;

      .content {
        padding-left: 50px;
      }
    }

    &.team {
      .content {
        display: flex;
        flex-wrap: wrap;

        .member {
          flex: 0 0 50%;
          margin-bottom: 100px;
          display: flex;
          flex-direction: column;

          &:nth-child(odd) {
            padding-right: 25px;
          }

          &:nth-child(even) {
            padding-left: 25px;
          }

          .image {
            margin-bottom: 50px;
            padding-top: 66.67%;
            position: relative;

            .photo {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              object-fit: cover;
              @include transition('fast', 'opacity');
            }
            
            .mask {
              z-index: 2;
              position: absolute;
              top: 0;
              left: 50%;
              height: 100%;
              transform: translateX(-50%);
              @include transition('fast', 'opacity');

              @media (hover: hover) {
                opacity: 0;
              }
            }
          }

          .job-position {
            margin-top: 10px;
            color: $green;
          }

          &:hover {
            @media (hover: hover) {
              .mask {
                opacity: 1;
              }
  
              .photo {
                opacity: 0;
              }
            }
          }
        }

        @media screen and (max-width: $breakpoint-xl) {
          .member {
            margin-bottom: 80px;
          }
        }

        @media screen and (max-width: $breakpoint-md) {
          .member {
            .image {
              margin-bottom: 30px;
            }

            .job-position {
              margin-top: 5px;
            }
          }
        }

        @media screen and (max-width: $breakpoint-sm) {
          padding-bottom: 100px;
        }

        @media screen and (max-width: $breakpoint-xs) {
          flex-direction: column;
          flex-wrap: nowrap;
  
          .member {
            width: 100%;
            flex: 0 0 100%;

            &:nth-child(odd) {
              padding: 0 0 25px 0;
            }
  
            &:nth-child(even) {
              padding: 25px 0 0 0;
            }
          }
        }
      }
    }

  }
}