@import "variables.scss";

@mixin text-body () {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: 0.18px;
  text-transform: none;
  text-decoration: none;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1px;
  }
}

@mixin text-caption () {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 28px;
  letter-spacing: 0.14px;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 10px;
    line-height: 16px;
    letter-spacing: 0.1px;
  }
}

@mixin text-label () {
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.5px;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 16px;
    letter-spacing: 0.44px;
  }
}

@mixin text-subheading () {
  font-family: 'Work Sans', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.5px;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.44px;
  }
}

@mixin text-h2 () {
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  font-size: 28px;
  line-height: 36px;
  letter-spacing: 0.25px;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.18px;
  }
}

@mixin text-h1 () {
  font-family: 'Work Sans', sans-serif;
  font-weight: 600;
  font-size: 36px;
  line-height: 60px;
  letter-spacing: 0.5px;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 26px;
    line-height: 34px;
    letter-spacing: 0.36px;
  }
}

@mixin text-xl () {
  font-family: 'Work Sans', sans-serif;
  font-weight: 600;
  font-size: 70px;
  line-height: 100px;
  letter-spacing: 1px;

  @media screen and (max-width: $breakpoint-md) {
    font-size: 36px;
    line-height: 50px;
    letter-spacing: 0.5px;
  }
}

@mixin text-xxl () {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 150px;
  line-height: 100px;
  letter-spacing: 2.08px;

  @media screen and (max-width: $breakpoint-xl) {
    font-size: 120px;
    line-height: 80px;
    letter-spacing: 1.5px;
  }

  @media screen and (max-width: $breakpoint-md) {
    font-size: 54px;
    line-height: 50px;
    letter-spacing: 0.75px;
  }
}

.text-caption {
  @include text-caption;
}

.text-label {
  @include text-label;
}

.text-subheading {
  @include text-subheading;
}

.text-h2 {
  @include text-h2;
}

.text-h1 {
  @include text-h1;
}

.text-xl {
  @include text-xl;
}

.text-xxl {
  @include text-xxl;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-p {
  margin-bottom: 1em;
}